<template>
  <el-container>
    <el-header style="height: 60px;text-align: right;font-size: 24px;color: #ffffff;">
      <el-dropdown>
        <i class="el-icon-setting" style="margin-right: 15px; text-align: left;"></i>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item @click.native="loginOut">退出登陆</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
      <span>{{username}}</span>
    </el-header>
    <el-container>
      <el-aside width="200px" style="background-color: rgb(238, 241, 246)">
        <el-menu :default-openeds="['1']">
          <el-submenu index="1">
            <template slot="title">
              <i class="el-icon-message"></i>挂号系统
            </template>
            <el-menu-item-group>
              <el-menu-item index="1-1" @click="click(1)">挂号</el-menu-item>
              <el-menu-item index="1-2" @click="click(2)">医生信息修改</el-menu-item>
              <el-menu-item index="1-3" @click="click(3)">挂号信息</el-menu-item>
              <!-- <el-menu-item index="1-3">回复我的</el-menu-item> -->
            </el-menu-item-group>
          </el-submenu>
        </el-menu>
      </el-aside>
      <el-main>
        <div class="order_page" v-if="s1">
          <order-page></order-page>
        </div>

        <div class="order_page" v-if="s2">
          <doctor-page></doctor-page>
        </div>

        <div class v-if="s3">
          <order-list></order-list>
        </div>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
import OrderPage from "@/components/OrderPage";
import DoctorPage from "@/components/DoctorPage";
import OrderList from "@/components/OrderList";

export default {
  components: {
    OrderPage,
    DoctorPage,
    OrderList
  },
  mounted() {
    this.username = this.$cookies.get("username");
    if (this.username === null) {
      this.$router.push({ path: "/login" });
    }
  },
  methods: {
    loginOut() {
      this.$cookies.remove("username");
      this.$router.push({ path: "/login" });

    },
    click(no) {
      this.s1 = false;
      this.s2 = false;
      this.s3 = false;
      if (no === 1) {
        this.s1 = true;
      } else if (no === 2) {
        this.s2 = true;
      } else if (no === 3) {
        this.s3 = true;
      }
      this.hackReset = false;
      this.$nextTick(() => {
        this.hackReset = true;
      });
    }
  },
  data() {
    return {
      s1: true,
      s2: false,
      s3: false,
      username: ''
    };
  }
};
</script>

<style scoped>
.el-header {
  background-color: #409effd6;
  color: #333;
  line-height: 60px;
}

.el-aside {
  color: #333;
}

.el-row {
  margin-bottom: 20px;
}
.order_page {
  padding-left: 20%;
  padding-right: 20%;
  padding-top: 50px;
}
</style>



