<template>
  <div style="padding-left: 50px;padding-right: 50px;">
    <template>
      <el-select
        style="width: 100%;"
        v-model="selectValue"
        filterable
        remote
        reserve-keyword
        placeholder="请输入挂号号码"
        :remote-method="remoteMethod"
        :loading="loading"
        @click.native="selectTrigger(this)"
      >
        <el-option
          v-for="item in selectList"
          :key="item.orderId"
          :label="item.orderId"
          :value="item.orderId"
        ></el-option>
      </el-select>
    </template>
    <el-dialog title="选择医生" :visible.sync="dialogFormVisible">
      <div class="el-dialog-body" style="padding-left: 100px;padding-right: 100px;">
        <el-cascader-panel @change="handleChange" :options="options" v-model="value"></el-cascader-panel>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogFormVisible = false">确 定</el-button>
      </div>
    </el-dialog>

    <el-dialog title="修改挂号信息" :visible.sync="editDialogFormVisible">
      <el-form>
        <el-form-item label="姓名">
          <el-input v-model="editOrder.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="年龄">
          <el-input v-model="editOrder.year" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="手机号">
          <el-input v-model="editOrder.tel" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="editDialogFormVisible = false;">取 消</el-button>
        <el-button type="primary" @click="updateOrder">确 定</el-button>
      </div>
    </el-dialog>

    <el-table :data="list">
      <el-table-column prop="orderId" label="单号"></el-table-column>
      <el-table-column prop="name" label="年龄"></el-table-column>
      <el-table-column prop="tel" label="手机号"></el-table-column>
      <el-table-column prop="year" label="年龄"></el-table-column>
      <el-table-column fixed="right" label="操作" width="100">
        <template slot-scope="scope">
          <el-button
            type="primary"
            icon="el-icon-edit"
            circle
            size="small"
            @click="handleEdit(scope.row)"
          ></el-button>
          <el-button
            type="danger"
            icon="el-icon-delete"
            circle
            size="small"
            @click="handleDelete(scope.row)"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>

    <div >
      <el-button style="margin-left: 92%;margin-top: 10px;" type="primary" @click="dialogFormVisible = true">选择医生</el-button>
    </div>
  </div>
</template>

<script>
export default {
  methods: {
    selectTrigger(e) {
      console.log(e);
      this.list = this.selectList;
    },
    remoteMethod(query) {
      this.loading = true;
      this.axios
        .get("/order/getByOrderId", {
          params: {
            id: query
          }
        })
        .then(res => {
          if (res.status === 200) {
            this.selectList = [];
            res = res.data.data;
            this.selectList.push(res);
            this.loading = false;
            // res.forEach(e => {
            //   this.selectList.push(e);
            // });
          }
        });
    },
    handleDelete(row) {
      this.axios
        .delete("/order?" + this.qs.stringify({ id: row.orderId }))
        .then(res => {
          if (res.status === 200) {
            res = res.data;
            if (res.code === 200) {
              this.$message({
                showClose: true,
                message: "恭喜你，这是一条成功消息",
                type: "success"
              });
              this.list.splice(
                this.list.findIndex(
                  item => item.orderId === this.editOrder.orderId
                )
              );
            } else {
              this.$message({
                showClose: true,
                message: "错了哦，这是一条错误消息",
                type: "error"
              });
            }
          } else {
            this.$message({
              showClose: true,
              message: "错了哦，这是一条错误消息",
              type: "error"
            });
          }
        });
    },

    updateOrder() {
      this.axios({
        url: "/order",
        method: "patch",
        data: this.qs.stringify(this.editOrder)
      }).then(res => {
        if (res.status === 200) {
          res = res.data;
          if (res.code === 200) {
            this.$message({
              showClose: true,
              message: "恭喜你，这是一条成功消息",
              type: "success"
            });
            this.editDialogFormVisible = false;
          } else {
            this.$message({
              showClose: true,
              message: "错了哦，这是一条错误消息",
              type: "error"
            });
          }
        } else {
          this.$message({
            showClose: true,
            message: "错了哦，这是一条错误消息",
            type: "error"
          });
        }
      });
    },

    handleEdit(row) {
      this.editDialogFormVisible = true;
      this.editOrder = row;
    },

    handleChange() {
      this.doctor.doctorId = this.value[1];
      this.order.doctorId = this.doctor.doctorId;
      this.axios
        .get("/order/getByDoctorId", {
          params: {
            id: this.doctor.doctorId
          }
        })
        .then(res => {
          if (res.status === 200) {
            res = res.data.data;
            this.list = [];
            res.forEach(e => {
              this.list.push(e);
            });
          }
        });
    }
  },

  mounted() {
    this.axios.get("/dept/list").then(res => {
      if (res.status === 200) {
        res.data.data.forEach(element => {
          //console.log(element);
          var temp = {
            value: element.deptId,
            label: element.deptName,
            children: []
          };
          this.axios
            .get("/doctor/list", {
              params: {
                id: element.deptId
              }
            })
            .then(res => {
              if (res.status === 200) {
                res.data.data.forEach(e => {
                  console.log(e);
                  var tempchilder = {
                    value: e.doctorId,
                    label: e.doctorName
                  };
                  temp.children.push(tempchilder);
                });
              }
            });
          this.options.push(temp);
        });
      }
    });
  },
  data() {
    return {
      loading: false,
      selectList: [],
      selectValue: "",
      editDialogFormVisible: false,
      dialogFormVisible: true,
      value: [],
      options: [],
      list: [],
      editOrder: {
        orderId: "",
        name: "",
        year: "",
        tel: "",
        doctorId: ""
      },
      order: {
        orderId: "",
        name: "",
        year: "",
        tel: "",
        doctorId: ""
      },
      doctor: {
        doctorId: "",
        doctorName: "",
        deptId: "",
        receiver: ""
      }
    };
  }
};
</script>



<style scoped>
.el-header {
  background-color: #b3c0d1;
  color: #333;
  line-height: 60px;
}

.el-aside {
  color: #333;
}
</style>


