<template>
  <div style="
    background: linear-gradient(to top left, #fff,#000);
    height: 100%;
">
    <div class="login-box">
      <el-main>
        <el-row>
          <el-col :span="8">
            <el-input id="name" v-model="userInfo.username" placeholder="请输入帐号">
              <template slot="prepend">帐号</template>
            </el-input>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="8">
            <el-input id="password" v-model="userInfo.password" type="password" placeholder="请输入密码">
              <template slot="prepend">密码</template>
            </el-input>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="8">
            <el-button id="login" @click="doLogin" style="width:100%" type="primary">登录</el-button>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="8">
            <el-button id="register" @click="doRegister" style="width:100%" type="warning">注册</el-button>
          </el-col>
        </el-row>
      </el-main>
    </div>
  </div>
</template>

<script>
export default {
  name: "login",
  data() {
    return {
      userInfo: {
        username: "",
        password: ""
      },
      show: false
    };
  },
  methods: {
    doLogin() {
      if (this.userInfo.username === "") {
        alert("用户名不能为空");
        return false;
      }
      if (this.userInfo.password === "") {
        alert("密码名不能为空");
        return false;
      }
      this.axios
        .post("/user/checkUser", this.qs.stringify(this.userInfo))
        .then(res => {
          if (res.status === 200) {
            res = res.data;
            if (res.code === 200) {
              var d = new Date();
              d.setTime(d.getTime() + 24 * 60 * 60 * 1000 * 1);
              this.$cookies.set("username", this.userInfo.username, { expires: 7, path: '/' });
              this.$router.replace("/");
            } else {
              this.$notify({
                title: "提示信息",
                message: res.message,
                type: "error"
              });
            }
          } else {
            this.$notify({
              title: "提示信息",
              message: "账号或密码错误",
              type: "error"
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    doRegister() {
      if (this.userInfo.username === "") {
        alert("用户名不能为空");
        return false;
      }
      if (this.userInfo.password === "") {
        alert("密码名不能为空");
        return false;
      }
      this.axios
        .post("/register", this.qs.stringify(this.userInfo))
        .then(res => {
          if (res.status === 200) {
            res = res.data;
            if (res.code === 200) {
              this.$notify({
                title: "提示信息",
                message: res.message,
                type: "success"
              });
            } else {
              this.$notify({
                title: "提示信息",
                message: res.message,
                type: "error"
              });
            }
          } else {
            this.$notify({
              title: "提示信息",
              message: "账号或密码错误",
              type: "error"
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.el-row {
  margin-bottom: 20px;
}

.login-box {
  padding-top: 15%;
  padding-left: 40%;
}
</style>



