<template>
  <div>
    <el-dialog title="选择医生" :visible.sync="dialogFormVisible">
      <div class="el-dialog-body" style="padding-left: 100px;padding-right: 100px;">
        <template>
          <el-select
            style="padding-bottom: 15px; width: 100%"
            v-model="selectValue"
            filterable
            remote
            reserve-keyword
            placeholder="请输入关键词"
            :remote-method="remoteMethod"
            :loading="loading"
            @click.native="selectTrigger(selectValue)"
            :change="selectTrigger(selectValue)"
          >
            <el-option
              v-for="item in selectList"
              :key="item.doctorId"
              :label="item.doctorName"
              :value="item.doctorId"
            ></el-option>
          </el-select>
        </template>
        <el-cascader-panel @change="handleChange" :options="options" v-model="value"></el-cascader-panel>
      </div>

      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogFormVisible = false">确 定</el-button>
      </div>
    </el-dialog>

    <el-form :model="doctor" label-position="right" label-width="80px">
      <el-form-item label="医生姓名" v-if="doctor.doctorId !== ''">
        <el-input v-model="doctor.doctorName"></el-input>
      </el-form-item>
      <el-form-item label="医生科室" v-if="doctor.doctorId !== ''">
        <el-select v-model="doctor.deptId" placeholder="科室调换">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="医生简介" v-if="doctor.doctorId !== ''">
        <el-input v-model="doctor.info"></el-input>
      </el-form-item>
    </el-form>
    <div style="right: 18%;margin-left: 75%;">
      <el-button
        style="position: absolute; right: 25%;"
        type="warning"
        @click="dialogFormVisible = true"
      >选择医生</el-button>
      <el-button style="position: absolute; right: 18%;" type="primary" @click="updateDoctor">修 改</el-button>
    </div>
  </div>
</template>

<script>
export default {
  methods: {
    selectTrigger(t) {
      console.log(t);

      this.selectList.forEach(e => {
        if (e.doctorId === t) {
          console.log(e);
          this.doctor = e;
        }
      });
      //this.list = this.selectList;
    },
    remoteMethod(query) {
      this.loading = true;
      this.axios
        .get("/doctorName", {
          params: {
            name: query
          }
        })
        .then(res => {
          if (res.status === 200) {
            this.selectList = [];
            res = res.data.data;
            if (res.length === 1) {
              this.selectList = res;
            } else {
              res.forEach(e => {
                this.selectList.push(e);
              });
            }
            this.loading = false;
          }
        });
    },

    handleChange() {
      this.doctor.doctorId = this.value[1];
      this.axios
        .get("/doctor", {
          params: {
            id: this.doctor.doctorId
          }
        })
        .then(res => {
          if (res.status === 200) {
            res = res.data.data;
            this.doctor = res;
          }
        });
    },
    updateDoctor() {
      this.axios.post("/doctor", this.qs.stringify(this.doctor)).then(res => {
        if (res.status === 200) {
          res = res.data;
          if (res.code === 200) {
            this.$message({
              showClose: true,
              message: "成功",
              type: "success"
            });
          } else {
            this.$notify({
              title: "提示信息",
              message: res.message,
              type: "error"
            });
          }
        }
      });
    }
  },

  mounted() {
    this.axios.get("/dept/list").then(res => {
      if (res.status === 200) {
        res.data.data.forEach(element => {
          var temp = {
            value: element.deptId,
            label: element.deptName,
            children: []
          };
          this.axios
            .get("/doctor/list", {
              params: {
                id: element.deptId
              }
            })
            .then(res => {
              if (res.status === 200) {
                res.data.data.forEach(e => {
                  console.log(e);
                  var tempchilder = {
                    value: e.doctorId,
                    label: e.doctorName
                  };
                  temp.children.push(tempchilder);
                });
              }
            });
          this.options.push(temp);
        });
      }
    });
  },
  data() {
    return {
      loading: false,
      selectValue: "",
      selectList: [],
      dialogFormVisible: true,
      value: [],
      options: [],

      doctor: {
        doctorId: "",
        doctorName: "",
        deptId: "",
        receiver: "",
        info: ""
      }
    };
  }
};
</script>



<style scoped>
.el-header {
  background-color: #b3c0d1;
  color: #333;
  line-height: 60px;
}

.el-aside {
  color: #333;
}
</style>



