<template>
  <div>
    <el-dialog title="选择医生" :visible.sync="dialogFormVisible">
      <div class="el-dialog-body" style="padding-left: 100px;padding-right: 100px;">
        <el-cascader-panel @change="handleChange" :options="options" v-model="value"></el-cascader-panel>
      </div>

      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogFormVisible = false">确 定</el-button>
      </div>
    </el-dialog>
    <template>
  <el-carousel :interval="4000" type="card" height="200px">
    <el-carousel-item v-for="item in imgs" :key="item">
      <el-image :src="item"></el-image>
    </el-carousel-item>
  </el-carousel>
</template>

    <el-form :model="order" label-position="right" label-width="80px">
      <div v-if="order.doctorId !== ''">
        <el-form-item label="医生姓名">
          <el-input v-model="doctor.doctorName" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="剩余号数">
          <el-input v-model="doctor.receiver" :disabled="true"></el-input>
        </el-form-item>
      </div>

      <el-form-item label="患者姓名">
        <el-input v-model="order.name"></el-input>
      </el-form-item>
      <el-form-item label="患者年龄">
        <el-input v-model="order.year"></el-input>
      </el-form-item>
      <el-form-item label="联系电话">
        <el-input v-model="order.tel"></el-input>
      </el-form-item>
    </el-form>
    <div style="right: 18%;margin-left: 75%;">
      <el-button type="warning" @click="dialogFormVisible = true">修改医生</el-button>
      <el-button type="primary" @click="addOrder">挂 号</el-button>
    </div>
  </div>
</template>

<script>
export default {
  methods: {
    handleChange() {
      this.doctor.doctorId = this.value[1];
      this.order.doctorId = this.doctor.doctorId;
      this.axios
        .get("/doctor", {
          params: {
            id: this.doctor.doctorId
          }
        })
        .then(res => {
          if (res.status === 200) {
            res = res.data.data;
            this.doctor.doctorId = res.doctorId;
            this.doctor.doctorName = res.doctorName;
            this.doctor.receiver = res.receiver;
            this.doctor.deptId = res.deptId;
          }
        });
    },

    addOrder() {
      this.axios.post("/order", this.qs.stringify(this.order)).then(res => {
        if (res.status === 200) {
          res = res.data;
          if (res.code === 200) {
            this.$message({
              showClose: true,
              message: "成功",
              type: "success"
            });
          } else {
            this.$notify({
              title: "提示信息",
              message: res.message,
              type: "error"
            });
          }
        }
      });
    }
  },

  mounted() {
    this.axios.get("/dept/list").then(res => {
      if (res.status === 200) {
        res.data.data.forEach(element => {
          //console.log(element);
          var temp = {
            value: element.deptId,
            label: element.deptName,
            children: []
          };
          this.axios
            .get("/doctor/list", {
              params: {
                id: element.deptId
              }
            })
            .then(res => {
              if (res.status === 200) {
                res.data.data.forEach(e => {
                  console.log(e);
                  var tempchilder = {
                    value: e.doctorId,
                    label: e.doctorName
                  };
                  temp.children.push(tempchilder);
                });
              }
            });
          this.options.push(temp);
        });
      }
    });
  },
  data() {
    return {
       imgs: [
         'http://s4.sinaimg.cn/middle/a9f45dc5tc65453b0a803&690',
         'https://pic1.shejiben.com/case/2013/03/28/20130328140453-1695ccb7.jpg'
       ],
      dialogFormVisible: true,
      value: [],
      options: [],
      order: {
        name: "",
        year: "",
        tel: "",
        doctorId: ""
      },
      doctor: {
        doctorId: "",
        doctorName: "",
        deptId: "",
        receiver: ""
      }
    };
  }
};
</script>



<style scoped>
.el-header {
  background-color: #b3c0d1;
  color: #333;
  line-height: 60px;
}

.el-aside {
  color: #333;
}
  .el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 200px;
    margin: 0;
  }
  
  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }
  
  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }
</style>



