<template>
  <router-view></router-view>
</template>

<script>
import Vue from "vue";
import Element from "element-ui";
import Home from "@/components/Home";

Vue.use(Element);

export default {
  name: "App",
  components: {
    Home
  }
};
</script>

<style>
html,
body,
#app,
.el-container {
  /*设置内部填充为0，几个布局元素之间没有间距*/
  padding: 0px;
  /*外部间距也是如此设置*/
  margin: 0px;
  /*统一设置高度为100%*/
  height: 100%;
}
</style>



